/* Navbar Styling*/
.NavbarItems {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
    padding: 0 30 px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.25);
    width: 100%;
    height: 80px;
    border-radius: 0px;
    position: absolute;    
    top: 0px;
    left: 50%;
    transform: translate(-50%);
}

.logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    margin-left: 10px;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(7, auto);
    grid-gap: 10px;
    list-style: none;
    align-items: center;
    text-align: center;
    justify-content: end;
}

.nav-links {
    text-decoration: none;
    color: #fff;
    padding: 0.7rem 1rem;
    white-space: nowrap;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
}

/* .nav-links:hover {
    background: rgb(240, 240, 240);
    border-radius: 4px;
    transition: all 0.2s ease-in-out;
} */

.fa-bars,
.fa-times {
    color: #fff;
    margin-right: 10px;
}

.menu-icons {
    display: none;
}

.nav-links-mobile {
    padding: 0.5rem 1rem;
    white-space: nowrap;
    background-color: #fafafa;
    border-radius: 3px;
    text-decoration: none;
    font-family: 'Helvetica';
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 20px;
    font-size: 1rem;
    color: #222;
    font-weight: 500;

}

/* Screen using less than 850 px */
@media screen and (max-width:850px) {
    .NavbarItems {
        z-index: 99;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        background: rgba(255, 255, 255, 0.25);
        box-shadow: 0 8px 32px 0 rgba(31, 38, 135, .37);
        backdrop-filter: blur(4px);
        border-radius: 13px;
        width: 100%;
        height: auto;
        position: absolute;
        top: 0;
        left: -110%;
        align-items: stretch;
        padding: 80px 0 30px 0;
        margin: 0;
        z-index: -1;
        transition: all 0.3s ease-in-out;

    }

    .nav-menu.active {
        left: 0%;
    }

    .nav-links {
        display: block;
        width: 100%;
        padding: 2rem 0;
        color: #222;
        font-family: 'Helvetica';
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 20px;
    }

    .nav-links:hover {
        background: rgba(255, 255, 255, 0.25);
        backdrop-filter: blur(20px);
    }

    .menu-icons {
        display: block;
    }

    .nav-links-mobile {
        padding: 1.5rem;
        display: block;
        text-align: center;
        font-size: 1.3rem;
        font-weight: bold;
        width: 80%;
        margin: auto;
    }
}